.client {
  display: flex;
}
.list .client {
  display: flex;
}
.client-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 170px;
  height: 90px;
}
.client-icon:not(:last-child) {
  margin-right: 30px;
}
.client-link {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  margin: auto;
  border: 1px solid var(--light);
  border-radius: 4px;
}
.client-link:hover {
  border: 1px solid var(--accent-background);
  transform: opasity;
}
.client-link:focus {
  border: 1px solid var(--accent-background);
  outline: 1px solid #757575;
  transform: opasity;
}

.client-logo-icon {
  width: 89px;
  height: 52px;
  fill: var(--light);
  transition: fill 250ms var(--timer);
}
.client-link:focus .client-logo-icon, .client-link:hover .client-logo-icon {
  fill: var(--accent-background);
  transform: opasity;
}

