.footer {
  background-color: var(--b-dark);
  min-height: 252px;
  padding-top: 60px;
  padding-bottom: 60px;
}
.footer .container {
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
}
.footer-address {
  font-weight: normal;
  font-size: 14px;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: 0.03em;
  margin-top: 20px;
}
.footer .social-list-icon {
  fill: var(--white);
}
.social-list-link:hover .footer .social-list-icon, .social-list-link:focus .footer .social-list-icon {
  fill: var(--white);
  transform: opasity;
}
.footer .social-list-link {
  background-color: var(--dark-background);
}
.footer .social-list-link:hover {
  background-color: var(--accent-background);
  transform: opasity;
}
.footer .social-list-link:focus {
  background-color: var(--accent-background);
  outline: 1px solid #757575;
  transform: opasity;
}

.contakt-box {
