  transform: opasity;
}
.link-addres:focus {
  color: var(--txt-bleu);
  outline: 1px solid #757575;
  transform: opasity;
}
.link-contact {
  color: rgba(255, 255, 255, 0.6);
  transition: color 250ms var(--timer);
}
.link-contact:hover {
  color: var(--txt-bleu);
  transform: opasity;
}
.link-contact:focus {
  color: var(--txt-bleu);
  outline: 1px solid #757575;
  transform: opasity;
}

