}

.form-contact {
  display: flex;
  position: relative;
}

.mailing-input {
  width: 358px;
  height: 50px;
  background-color: var(--b-dark);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  margin-right: 12px;
  padding-left: 16px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.mailing-input:focus {
  outline: none;
  color: var(--txt-grey);
  border: 1px solid var(--accent-background);
}
.mailing-box {
  display: flex;
