}
.mailing-box .btn {
  display: flex;
  width: 200px;
  height: 50px;
  padding: 10px 28px;
  color: var(--white);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  transition: color 250ms var(--timer);
}

.btn-follow-mailing:focus,
.btn-follow-mailing:hover {
  cursor: pointer;
  color: var(--txt-dark);
  transform: opasity;
}

.icon-send-fly {
  position: absolute;
  right: 28px;
  top: 13px;
  display: flex;
  fill: var(--txt-light);
